<template>
  <div>
    <v-container grid-list-md text-xs-center>
      <v-layout row wrap>
        <v-flex xs6>
          <v-list subheader>
            <v-list-tile avatar>
              <v-list-tile-avatar>
                <v-icon>music_note</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ songData.genre }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile avatar>
              <v-list-tile-avatar>
                <v-icon>title</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ songData.title }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile avatar>
              <v-list-tile-avatar>
                <v-icon>person</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ songData.artist }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile avatar>
              <v-list-tile-avatar>
                <v-icon>fast_forward</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ songData.maxbpm == songData.minbpm ? songData.maxbpm : songData.minbpm + "-" + songData.maxbpm }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile avatar>
              <v-list-tile-avatar>
                <v-icon>stars</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>☆{{ songData.level }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile avatar>
              <v-list-tile-avatar>
                <v-icon>assessment</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ songData.notes }} Notes&nbsp;/&nbsp;{{ songData.total }}&nbsp;total</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
        </v-flex>
        <v-flex xs6>
          <youtube :video-id="videoId" player-width="560" player-height="345"></youtube>
        </v-flex>
      </v-layout>
    </v-container>
    <v-container text-xs-center>
      <v-layout row wrap>
        <v-flex xs12>
          <v-progress-circular
            :size="50"
            color="primary"
            indeterminate
            v-show="progress"
          ></v-progress-circular>
          <div id="table"></div>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
/* eslint-disable */
import AWS from "aws-sdk";
import axios from "axios";

export default {
  name: "Song",
  watch: {
    $route(to, from) {
      this.getSongData();
    }
  },
  props: {
    msg: String
  },
  mounted: function() {
    this.getSongData();
  },
  data() {
    return {
      table: null,
      progress: false,
      songData: {},
      videoId: ""
    };
  },
  methods: {
    getYoutubeId: function(title) {
      let self = this;
      let url =
        "https://www.googleapis.com/youtube/v3/search?type=video&part=id&q=" +
        encodeURIComponent(title + "BMS") +
        "&key=AIzaSyAhzvSZ6LQXhjOJz2xd5W4gZcoe-WCe6rs";

      axios.get(url).then(function(e) {
        if (e.data.items.length == 0) {
          url =
            "https://www.googleapis.com/youtube/v3/search?type=video&part=id&q=" +
            encodeURIComponent(title) +
            "&key=AIzaSyAhzvSZ6LQXhjOJz2xd5W4gZcoe-WCe6rs";
          axios.get(url).then(function(ev) {
            if (ev.data.items.length != 0) {
              self.videoId = ev.items[0].id.videoId;
            }
          });
        } else {
          self.videoId = e.data.items[0].id.videoId;
        }
      });
    },
    getSongHash: function() {
      return this.$route.params.songhash;
    },
    getLnMode: function() {
      return this.$route.params.lnmode;
    },
    createTable: function(notes) {
      this.table = new Tabulator("#table", {
        layout: "fitColumns",
        rowFormatter: function(row) {
          if (row.getData().novalidate) {
            row.getElement().style.backgroundColor = "#A6A6DF";
          }
        },
        columns: [
          {
            formatter: "rownum",
            align: "center",
            width: 40,
            headerSort: false
          },
          {
            title: "Name",
            field: "username",
            width: 150,
            headerSort: false,
            formatter: function(cell, formatterParams) {
              let data = cell.getRow().getData();
              let userid = data.userid;
              return `<a href="./#/viewer/user/${userid}">${cell.getValue()}</a>`;
            }
          },
          {
            title: "Score",
            field: "score",
            align: "left",
            formatter: "progress",
            formatterParams: { min: 0, max: notes * 2, legend: true },
            headerSort: false
          },
          {
            title: "Combo",
            field: "combo",
            align: "left",
            formatter: "progress",
            formatterParams: {
              min: 0,
              max: notes,
              color: "orange",
              legend: true
            },
            headerSort: false
          },
          {
            title: "Clear",
            field: "clear",
            align: "center",
            width: 150,
            headerSort: false,
            formatter: function(cell, formatterParams) {
              switch (cell.getValue()) {
                case 0:
                  cell.getElement().style.backgroundColor =
                    "rgb(255, 255, 255)";
                  return "NoPlay";
                case 1:
                  cell.getElement().style.backgroundColor =
                    "rgb(192, 192, 192)";
                  return "Failed";
                case 2:
                  cell.getElement().style.backgroundColor =
                    "rgb(149, 149, 255)";
                  return "AssistEasy";
                case 3:
                  cell.getElement().style.backgroundColor =
                    "rgb(149, 149, 255)";
                  return "LightAssistEasy";
                case 4:
                  cell.getElement().style.backgroundColor =
                    "rgb(152, 251, 152)";
                  return "Easy";
                case 5:
                  cell.getElement().style.backgroundColor =
                    "rgb(152, 251, 152)";
                  return "Normal";
                case 6:
                  cell.getElement().style.backgroundColor = "rgb(255, 99, 71)";
                  return "Hard";
                case 7:
                  cell.getElement().style.backgroundColor = "rgb(255, 217, 0)";
                  return "ExHard";
                case 8:
                  cell.getElement().style.backgroundColor = "rgb(255, 140, 0)";
                  return "FullCombo";
                case 9:
                  cell.getElement().style.backgroundColor = "rgb(255, 140, 0)";
                  return "Perfect";
                case 10:
                  cell.getElement().style.backgroundColor = "rgb(255, 140, 0)";
                  return "Max";
              }
            }
          }
        ]
      });
    },
    getSongData: function() {
      this.progress = true;
      const songhash = this.getSongHash();
      const lnmode = this.getLnMode();

      const self = this;

      AWS.config.update({
        accessKeyId: "AKIAIUNY56NANW7JGMTA",
        secretAccessKey: "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"
      });
      AWS.config.region = "us-east-1";
      let lambda = new AWS.Lambda();
      let params = {
        FunctionName: "get_song_data",
        Payload: JSON.stringify({
          songhash: songhash + (lnmode ? "." + lnmode : ".0")
        })
      };

      lambda.invoke(params, function(err, data) {
        self.progress = false;
        if (!err) {
          const json = JSON.parse(data.Payload.toString());
          if (json.message == "success") {
            self.songData = json.SongData;
            self.createTable(json.SongData.notes);
            self.table.setData(json.IRDatas);
            self.getYoutubeId(json.SongData.title);
          }
        }
      });
    }
  }
};
</script>

