<template>
  <div>
    <v-container grid-list-md text-xs-center>
      <v-layout row wrap>
        <v-flex xs6>
          <v-list subheader>
            <v-list-tile avatar>
              <v-list-tile-avatar>
                <v-icon>title</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ courceData.name }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile avatar>
              <v-list-tile-avatar>
                <v-icon>lock</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ courceData.constraints }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
        </v-flex>
        <v-flex xs6>
          <div id="songTable"></div>
        </v-flex>
      </v-layout>
    </v-container>
    <v-container text-xs-center>
      <v-layout row wrap>
        <v-flex xs12>
          <v-progress-circular
            :size="50"
            color="primary"
            indeterminate
            v-show="progress"
          ></v-progress-circular>
          <div id="table"></div>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
/* eslint-disable */
import AWS from "aws-sdk";
import axios from "axios";

export default {
  name: "Cource",
  watch: {
    $route(to, from) {
      this.getCourceData();
    }
  },
  props: {
    msg: String
  },
  mounted: function() {
    this.getCourceData();
  },
  data() {
    return {
      table: null,
      songTable: null,
      progress: false,
      courceData: {}
    };
  },
  methods: {
    getCourceHash: function() {
      return this.$route.params.courcehash;
    },
    getLnMode: function() {
      return this.$route.params.lnmode;
    },
    createSongTable: function() {
      this.songTable = new Tabulator("#songTable", {
        height: "300px",
        layout: "fitColumns",
        columns: [
          {
            formatter: "rownum",
            align: "center",
            width: 40,
            headerSort: false
          },
          { title: "SongTitle", field: "title", headerSort: false },
          {
            title: "IR - default",
            field: "songhash",
            width: 100,
            formatter: function(cell, formatterParams) {
              let value = cell.getValue();
              return `<a href="./#/viewer/song/${value}/0">IR</a>`;
            },
            headerSort: false
          },
          {
            title: "IR - CN",
            field: "songhash",
            width: 100,
            formatter: function(cell, formatterParams) {
              let value = cell.getValue();
              return `<a href="./#/viewer/song/${value}/1">IR(CN)</a>`;
            },
            headerSort: false
          },
          {
            title: "IR - HCN",
            field: "songhash",
            width: 100,
            formatter: function(cell, formatterParams) {
              let value = cell.getValue();
              return `<a href="./#/viewer/song/${value}/2">IR(HCN)</a>`;
            },
            headerSort: false
          }
        ]
      });
    },
    createTable: function(notes) {
      this.table = new Tabulator("#table", {
        layout: "fitColumns",
        rowFormatter: function(row) {
          if (row.getData().novalidate) {
            row.getElement().style.backgroundColor = "#A6A6DF";
          }
        },
        columns: [
          {
            formatter: "rownum",
            align: "center",
            width: 40,
            headerSort: false
          },
          { title: "Name", field: "username", width: 150, headerSort: false },
          {
            title: "Score",
            field: "score",
            align: "left",
            formatter: "progress",
            formatterParams: { min: 0, max: notes * 2, legend: true },
            headerSort: false
          },
          {
            title: "Combo",
            field: "combo",
            align: "left",
            formatter: "progress",
            formatterParams: {
              min: 0,
              max: notes,
              color: "orange",
              legend: true
            },
            headerSort: false
          },
          {
            title: "Clear",
            field: "clear",
            align: "center",
            width: 150,
            headerSort: false,
            formatter: function(cell, formatterParams) {
              switch (cell.getValue()) {
                case 0:
                  cell.getElement().style.backgroundColor =
                    "rgb(255, 255, 255)";
                  return "NoPlay";
                case 1:
                  cell.getElement().style.backgroundColor =
                    "rgb(192, 192, 192)";
                  return "Failed";
                case 2:
                  cell.getElement().style.backgroundColor =
                    "rgb(149, 149, 255)";
                  return "AssistEasy";
                case 3:
                  cell.getElement().style.backgroundColor =
                    "rgb(149, 149, 255)";
                  return "LightAssistEasy";
                case 4:
                  cell.getElement().style.backgroundColor =
                    "rgb(152, 251, 152)";
                  return "Easy";
                case 5:
                  cell.getElement().style.backgroundColor =
                    "rgb(152, 251, 152)";
                  return "Normal";
                case 6:
                  cell.getElement().style.backgroundColor = "rgb(255, 99, 71)";
                  return "Hard";
                case 7:
                  cell.getElement().style.backgroundColor = "rgb(255, 217, 0)";
                  return "ExHard";
                case 8:
                  cell.getElement().style.backgroundColor = "rgb(255, 140, 0)";
                  return "FullCombo";
                case 9:
                  cell.getElement().style.backgroundColor = "rgb(255, 140, 0)";
                  return "Perfect";
                case 10:
                  cell.getElement().style.backgroundColor = "rgb(255, 140, 0)";
                  return "Max";
              }
            }
          }
        ]
      });
    },
    getCourceData: function() {
      this.progress = true;
      const courcehash = this.getCourceHash();
      const lnmode = this.getLnMode();

      const self = this;

      AWS.config.update({
        accessKeyId: "AKIAIUNY56NANW7JGMTA",
        secretAccessKey: "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"
      });
      AWS.config.region = "us-east-1";
      let lambda = new AWS.Lambda();
      let params = {
        FunctionName: "get_cource_data",
        Payload: JSON.stringify({
          songhash: courcehash + (lnmode ? "." + lnmode : ".0")
        })
      };

      lambda.invoke(params, function(err, data) {
        self.progress = false;
        if (!err) {
          const json = JSON.parse(data.Payload.toString());
          if (json.message == "success") {
            self.courceData = json.CourceData;
            self.createTable(json.IRDatas[0].notes);
            self.table.setData(json.IRDatas);
            self.createSongTable();
            self.songTable.setData(json.CourceData.songs);
          }
        }
      });
    }
  }
};
</script>

