<template>
  <div>
    <v-snackbar
      v-model="snackbar"
      :timeout="2000"
      :top="true"
    >
      {{snackbarText}}
    </v-snackbar>
    <v-container grid-list-md>
      <!-- No Login -->
      <v-layout row wrap v-if="!accesstoken || !refreshtoken">
        <v-flex xs12>
          <v-card>
            <v-card-title primary-title>
              <div>
                <h2 class="display-2">{{username}}</h2>
                <div class="pa-4" :inner-html.prop="bio | nl2br | replaseURL"></div>
              </div>
            </v-card-title>
          </v-card>
        </v-flex>
      </v-layout>
      <!-- [Login] other user page  -->
      <v-layout row wrap v-if="accesstoken && refreshtoken && getUserId() != userid">
        <v-flex xs10>
          <v-card>
            <v-card-title primary-title>
              <div>
                <h2 class="display-2">{{username}}</h2>
                <div class="pa-4" :inner-html.prop="bio | nl2br | replaseURL"></div>
              </div>
            </v-card-title>
          </v-card>
        </v-flex>
        <v-flex xs2>
          <v-card>
            <v-btn 
              :loading="addRivalLoading"
              :disabled="addRivalLoading"
              color="primary" 
              @click.native="addRival()">Add Rival</v-btn>
            <v-btn 
              :loading="removeRivalLoading"
              :disabled="removeRivalLoading"
              color="error" 
              @click.native="removeRival()">Remove Rival</v-btn>
          </v-card>
        </v-flex>
      </v-layout>
      <!-- [Login] user page  -->
      <v-layout row wrap v-if="accesstoken && refreshtoken && getUserId() == userid">
        <v-flex xs12>
          <v-card>
            <v-card-title primary-title>
              <div>
                <h2 class="display-2">{{username}}</h2>
                <h4>User ID : {{userid}}</h4>
              </div>
            </v-card-title>
            <v-card-text>
              <v-textarea
                v-model="bio"
                auto-grow
                box
                label="Bio"
              ></v-textarea>
              <v-btn 
                :loading="updateBioLoading"
                :disabled="updateBioLoading"
                color="primary" 
                @click.native="updateBio()">Update</v-btn>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex xs6>
          <v-card>
            <v-card-text>
              <v-layout row>
                <v-flex p>
                  <v-btn 
                    :loading="generateApiKeyLoading"
                    :disabled="generateApiKeyLoading"
                    color="primary" 
                    @click.native="generateApiKey()">Generate API key</v-btn>
                </v-flex>
                <v-flex xs12>
                  <v-text-field
                    :readonly="true"
                    v-model="publickey"
                  ></v-text-field>
                </v-flex>
              </v-layout>
           </v-card-text>
          </v-card>
        </v-flex>
        <v-flex xs6>
          <v-card>
            <v-card-text>
              <h3>Rivals</h3>
              <div :id="'rivalTable'" ></div>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
      <v-layout row wrap>
        <v-flex xs12>
          <v-progress-circular
            :size="50"
            color="primary"
            indeterminate
            v-show="progress"
          ></v-progress-circular>
          <div :id="'table'" ></div>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
/* eslint-disable */
import AWS from "aws-sdk";

export default {
  name: "User",
  props: {
    accesstoken: String,
    refreshtoken: String,
    userid: String
  },
  watch: {
    $route(to, from) {
      this.getUserData();
    },
    accesstoken: function(newValue, oldValue) {
      this.getUserData();
    },
    refreshtoken: function(newValue, oldValue) {
      this.getUserData();
    },
    userid: function(newValue, oldValue) {
      this.getUserData();
    }
  },
  filters: {
    replaseURL: function(value) {
      if (typeof value === "undefined" || value === null) {
        return "";
      }
      const exp = /(\b(https?|ftp|file):\/\/[-A-Z0-9+&@#\/%?=~_|!:,.;]*[-A-Z0-9+&@#\/%=~_|])/gi;
      return value.replace(exp, "<a href='$1' target='_blank'>$1</a>");
    },
    nl2br: function(value) {
      if (typeof value === "undefined" || value === null) {
        return "";
      }
      const breakTag = "<br>";
      return (value + "").replace(
        /([^>\r\n]?)(\r\n|\n\r|\r|\n)/g,
        "$1" + breakTag + "$2"
      );
    }
  },
  mounted: function() {
    this.getUserData();
  },
  data() {
    return {
      snackbar: false,
      snackbarText: "",
      table: null,
      rivalTable: null,
      progress: false,
      username: "",
      bio: "",
      publickey: "",
      addRivalLoading: false,
      removeRivalLoading: false,
      updateBioLoading: false,
      generateApiKeyLoading: false,
    };
  },
  methods: {
    getUserId: function() {
      return this.$route.params.userid;
    },
    createTable: function(tabledata) {
      this.table = new Tabulator("#table", {
        layout: "fitColumns",
        columns: [
          {
            formatter: "rownum",
            align: "center",
            width: 40,
            headerSort: false
          },
          { title: "Title", field: "title", headerSort: false },
          { title: "Artist", field: "artist", headerSort: false },
          {
            title: "IR - default",
            field: "songhash",
            width: 100,
            formatter: function(cell, formatterParams) {
              let value = cell.getValue();
              let songhashArray = value.split(".");
              return `<a href="./#/viewer/song/${songhashArray[0]}/0">IR</a>`;
            },
            headerSort: false
          },
          {
            title: "IR - CN",
            field: "songhash",
            width: 100,
            formatter: function(cell, formatterParams) {
              let value = cell.getValue();
              let songhashArray = value.split(".");
              return `<a href="./#/viewer/song/${
                songhashArray[0]
              }/1">IR(CN)</a>`;
            },
            headerSort: false
          },
          {
            title: "IR - HCN",
            field: "songhash",
            width: 100,
            formatter: function(cell, formatterParams) {
              let value = cell.getValue();
              let songhashArray = value.split(".");
              return `<a href="./#/viewer/song/${
                songhashArray[0]
              }/2">IR(HCN)</a>`;
            },
            headerSort: false
          }
        ]
      });
      this.table.setData(tabledata);
    },
    createRivalTable: function(tabledata) {
      this.rivalTable = new Tabulator("#rivalTable", {
        layout: "fitColumns",
        columns: [
          {
            title: "Name",
            field: "username",
            headerSort: false,
            formatter: function(cell, formatterParams) {
              let data = cell.getRow().getData();
              let username = cell.getValue();
              let userid = data.userid;
              return `<a href="./#/viewer/user/${userid}">${username}</a>`;
            }
          }
        ]
      });
      this.rivalTable.setData(tabledata);
    },
    getUserData: function() {
      let userid = this.getUserId();
      let AccessToken = this.accesstoken;
      let RefreshToken = this.refreshtoken;
      let loginUserId = this.userid;

      if (AccessToken && RefreshToken && userid == loginUserId) {
        this.getUserDataLogin();
        return;
      }

      AWS.config.update({
        accessKeyId: "AKIAIUNY56NANW7JGMTA",
        secretAccessKey: "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"
      });
      AWS.config.region = "us-east-1";
      let lambda = new AWS.Lambda();
      let params = {
        FunctionName: "get_user_data",
        Payload: JSON.stringify({
          userId: userid
        })
      };

      const self = this;
      lambda.invoke(params, function(err, data) {
        if (err) {
          return;
        } else {
          let json = JSON.parse(data.Payload.toString());
          if (json.message == "success") {
            self.createTable(json.ScoreDatas);
            self.username = json.UserData.username;
            self.bio = json.UserData.bio;
          } else {
            return;
          }
        }
      });
    },
    getUserDataLogin: function() {
      let userid = this.getUserId();
      let AccessToken = this.accesstoken;
      let RefreshToken = this.refreshtoken;

      AWS.config.update({
        accessKeyId: "AKIAJGA7ZWB5TQNJMNCA",
        secretAccessKey: "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"
      });
      AWS.config.region = "us-east-1";
      let lambda = new AWS.Lambda();
      let params = {
        FunctionName: "get_user_data_login",
        Payload: JSON.stringify({
          AccessToken: AccessToken,
          RefreshToken: RefreshToken
        })
      };

      const self = this;
      lambda.invoke(params, function(err, data) {
        if (err) {
          return;
        } else {
          let json = JSON.parse(data.Payload.toString());
          if (json.message == "success") {
            self.createTable(json.ScoreDatas);
            self.createRivalTable(json.UserData.rivals);
            self.username = json.UserData.username;
            self.bio = json.UserData.bio;
            self.publickey = json.UserData.publickey;
          } else {
            return;
          }
        }
      });
    },
    generateApiKey: function() {
      this.generateApiKeyLoading = true;
      let AccessToken = this.accesstoken;
      let RefreshToken = this.refreshtoken;

      AWS.config.update({
        accessKeyId: "AKIAJGA7ZWB5TQNJMNCA",
        secretAccessKey: "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"
      });
      AWS.config.region = "us-east-1";
      let lambda = new AWS.Lambda();
      let params = {
        FunctionName: "generate_access_token",
        Payload: JSON.stringify({
          AccessToken: AccessToken,
          RefreshToken: RefreshToken
        })
      };

      const self = this;
      lambda.invoke(params, function(err, data) {
        self.generateApiKeyLoading = false;
        if (err) {
          self.snackbarText = "Failed to generate API KEY.";
          self.snackbar = true;
          return;
        } else {
          let json = JSON.parse(data.Payload.toString());
          if (json.message == "success") {
            if (json.publickey) {
              self.publickey = json.publickey;
            }
            self.$emit("rewriteToken", {
              accessToken: json.AccessToken,
              refreshToken: json.RefreshToken
            });
            self.snackbarText = "Successfully generated API KEY.";
            self.snackbar = true;
          } else {
            self.snackbarText = "Failed to generate API KEY.";
            self.snackbar = true;
            return;
          }
        }
      });
    },
    updateBio: function() {
      this.updateBioLoading = true;
      let AccessToken = this.accesstoken;
      let RefreshToken = this.refreshtoken;

      let bio = this.bio;
      AWS.config.update({
        accessKeyId: "AKIAJGA7ZWB5TQNJMNCA",
        secretAccessKey: "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"
      });
      AWS.config.region = "us-east-1";
      let lambda = new AWS.Lambda();
      let params = {
        FunctionName: "update_user_data",
        Payload: JSON.stringify({
          bio: bio,
          AccessToken: AccessToken,
          RefreshToken: RefreshToken
        })
      };

      const self = this;
      lambda.invoke(params, function(err, data) {
        self.updateBioLoading = false;
        if (err) {
          self.snackbarText = "Failed to update bio.";
          self.snackbar = true;
          return;
        } else {
          let json = JSON.parse(data.Payload.toString());
          if (json.message == "success") {
            self.$emit("rewriteToken", {
              accessToken: json.AccessToken,
              refreshToken: json.RefreshToken
            });
            self.snackbarText = "Successfully updated bio.";
            self.snackbar = true;
          } else {
            self.snackbarText = "Failed to update bio.";
            self.snackbar = true;
            return;
          }
        }
      });
    },
    addRival: function() {
      this.addRivalLoading = true;
      let userid = this.getUserId();
      let AccessToken = this.accesstoken;
      let RefreshToken = this.refreshtoken;

      AWS.config.update({
        accessKeyId: "AKIAJGA7ZWB5TQNJMNCA",
        secretAccessKey: "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"
      });
      AWS.config.region = "us-east-1";
      let lambda = new AWS.Lambda();
      let params = {
        FunctionName: "add_rival",
        Payload: JSON.stringify({
          AccessToken: AccessToken,
          RefreshToken: RefreshToken,
          rivalId: userid
        })
      };

      const self = this;
      lambda.invoke(params, function(err, data) {
        self.addRivalLoading = false;
        if (err) {
          self.snackbarText = "Failed to add rival.";
          self.snackbar = true;
          return;
        } else {
          let json = JSON.parse(data.Payload.toString());
          if (json.message == "success") {
            self.snackbarText = "Successfully added rival.";
            self.snackbar = true;
            return;
          } else {
            self.snackbarText = "Failed to add rival.";
            self.snackbar = true;
            return;
          }
        }
      });
    },
    removeRival: function() {
      this.removeRivalLoading = true;
      let userid = this.getUserId();
      let AccessToken = this.accesstoken;
      let RefreshToken = this.refreshtoken;

      AWS.config.update({
        accessKeyId: "AKIAJGA7ZWB5TQNJMNCA",
        secretAccessKey: "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"
      });
      AWS.config.region = "us-east-1";
      let lambda = new AWS.Lambda();
      let params = {
        FunctionName: "remove_rival",
        Payload: JSON.stringify({
          AccessToken: AccessToken,
          RefreshToken: RefreshToken,
          rivalId: userid
        })
      };

      const self = this;
      lambda.invoke(params, function(err, data) {
        self.removeRivalLoading = false;
        if (err) {
          self.snackbarText = "Failed to remove rival.";
          self.snackbar = true;
          return;
        } else {
          let json = JSON.parse(data.Payload.toString());
          if (json.message == "success") {
            self.snackbarText = "Successfully removed rival.";
            self.snackbar = true;
            return;
          } else {
            self.snackbarText = "Failed to remove rival.";
            self.snackbar = true;
            return;
          }
        }
      });
    }
  }
};
</script>

