<template>
  <v-app>
    <v-snackbar
      v-model="snackbar"
      :timeout="2000"
      :top="true"
    >
      Logout.
    </v-snackbar>
    <v-navigation-drawer
      clipped-left
      fixed
      app
      v-model="drawer"
    >
      <v-list>

        <v-list-tile>
          <v-list-tile-title class="title">
            {{ title }}
          </v-list-tile-title>
        </v-list-tile>

        <v-list-tile
          :to="'/'"
        >
          <v-list-tile-action>
            <v-icon>home</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>Home</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>

        <v-list-tile
          :to="'/about'"
        >
          <v-list-tile-action>
            <v-icon>bubble_chart</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>About</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>

        <v-list-tile
          :href="'https://drive.google.com/open?id=1wYVNufrfiVAWUkYTVdGl0Q0PqrhkHZBo'"
          target="_blank"
        >
          <v-list-tile-action>
            <v-icon>get_app</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>Download API</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>

        <v-list-tile
          :to="'/privacy'"
        >
          <v-list-tile-action>
            <v-icon>lock</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>PrivacyPolicy</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        
        <v-divider></v-divider>
        
        <v-subheader>Latest 100 played</v-subheader>
        <v-list-tile
          v-for="(item, i) in played"
          :key="i"
          :to="'/viewer/latest/'+item.key"
        >
          <v-list-tile-action>
            <v-icon>library_music</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title v-text="item.title"></v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>

        <v-divider></v-divider>

        <v-list-tile
          :to="'/viewer/cource-latest/'"
        >
          <v-list-tile-action>
            <v-icon>library_music</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>Cource</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar
      app
    >
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <v-spacer></v-spacer>
      <v-toolbar-items>
        <v-btn v-if="!accessToken || !refreshToken" flat @click="onSignUpOpen"><v-icon>person_add</v-icon>Sign Up</v-btn> 
        <v-btn v-if="!accessToken || !refreshToken" flat @click="onLoginOpen"><v-icon>computer</v-icon>Login</v-btn> 
        <v-btn v-if="accessToken && refreshToken" flat @click="logout"><v-icon>exit_to_app</v-icon>Logout</v-btn>
        <v-btn v-if="accessToken && refreshToken" flat :to="'/viewer/user/' + userId"><v-icon>settings</v-icon>Setting</v-btn>
      </v-toolbar-items>
    </v-toolbar>
    <SignUp ref="SignUp"></SignUp>
    <Login ref="Login" v-on:updateToken="updateToken"></Login>
    <v-content>
      <router-view 
        :accesstoken="accessToken"
        :refreshtoken="refreshToken"
        :userid="userId"
        v-on:rewriteToken="rewriteToken"></router-view>
    </v-content>
  </v-app>
</template>

<script>
import Vue from 'vue';
import SignUp from "./components/SignUp";
import Login from "./components/Login";

export default {
  name: "App",
  components: {
    SignUp,
    Login
  },
  data() {
    return {
      snackbar: false,
      accessToken: null,
      refreshToken: null,
      userId: null,
      drawer: false,
      played: [
        { title: "7KEYS", key: "beat-7k" },
        { title: "5KEYS", key: "beat-5k" },
        { title: "10KEYS", key: "beat-10k" },
        { title: "14KEYS", key: "beat-14k" },
        { title: "PMS 5KEYS", key: "popn-5k" },
        { title: "PMS 9KEYS", key: "popn-9k" },
        { title: "KM 24KEYS", key: "keyboard-24k" },
        { title: "KM 48KEYS", key: "keyboard-48k" }
      ],
      title: "MinIR"
    };
  },
  mounted: function() {
    this.accessToken = Vue.cookie.get("AccessToken");
    this.refreshToken = Vue.cookie.get("RefreshToken");
    this.userId = Vue.cookie.get("UserId");
  },
  methods: {
    onSignUpOpen: async function() {
      await this.$refs.SignUp.open();
    },
    onLoginOpen: async function() {
      await this.$refs.Login.open();
    },
    updateToken: function(value) {
      this.accessToken = value.accessToken;
      this.refreshToken = value.refreshToken;
      this.userId = value.userId;
      Vue.cookie.set("AccessToken", this.accessToken, 1);
      Vue.cookie.set("RefreshToken", this.refreshToken, 1);
      Vue.cookie.set("UserId", this.userId, 1);
    },
    rewriteToken: function(value){
      this.accessToken = value.accessToken;
      this.refreshToken = value.refreshToken;
      Vue.cookie.set("AccessToken", this.accessToken, 1);
      Vue.cookie.set("RefreshToken", this.refreshToken, 1);
    },
    logout: function() {
      this.accessToken = null;
      this.refreshToken = null;
      Vue.cookie.delete('AccessToken');
      Vue.cookie.delete('RefreshToken');
      this.userId = null;
      this.snackbar = true;
    }
  }
};
</script>
