<template>
  <v-container text-xs-center>
      <v-layout row wrap>
        <v-flex xs12>
          <v-progress-circular
            :size="50"
            color="primary"
            indeterminate
            v-show="progress"
          ></v-progress-circular>
          <div :id="'table'" ></div>
        </v-flex>
      </v-layout>
    </v-container>
</template>

<script>
/* eslint-disable */
import AWS from "aws-sdk";

export default {
  name: "Latest",
  watch: {
    '$route' (to, from) {
      this.getLatestData();
    }
  },
  props: {
    msg: String
  },
  mounted: function() {
    this.table = new Tabulator("#table", {
      layout: "fitColumns",
      columns: [
        { formatter: "rownum", align: "center", width: 40, headerSort: false },
        { title: "Title", field: "title", headerSort: false },
        { title: "Artist", field: "artist", headerSort: false },
        {
          title: "IR - default",
          field: "songhash",
          width: 100,
          formatter: function(cell, formatterParams) {
            let value = cell.getValue();
            return `<a href="./#/viewer/song/${value}/0">IR</a>`;
          },
          headerSort: false
        },
        {
          title: "IR - CN",
          field: "songhash",
          width: 100,
          formatter: function(cell, formatterParams) {
            let value = cell.getValue();
            return `<a href="./#/viewer/song/${value}/1">IR(CN)</a>`;
          },
          headerSort: false
        },
        {
          title: "IR - HCN",
          field: "songhash",
          width: 100,
          formatter: function(cell, formatterParams) {
            let value = cell.getValue();
            return `<a href="./#/viewer/song/${value}/2">IR(HCN)</a>`;
          },
          headerSort: false
        }
      ]
    });

    this.getLatestData();
  },
  data() {
    return {
      table: null,
      progress: false
    };
  },
  methods: {
    getMode: function(){
      return this.$route.params.mode;
    },
    getLatestData: function() {
      this.progress = true;
      let mode = this.getMode();
      const self = this;
      this.table.setData({});

      AWS.config.update({
        accessKeyId: "AKIAIUNY56NANW7JGMTA",
        secretAccessKey: "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"
      });
      AWS.config.region = "us-east-1";
      var lambda = new AWS.Lambda();
      var params = {
        FunctionName: "get_played_100",
        Payload: JSON.stringify({
          mode: mode ? mode : "beat-7k"
        })
      };

      lambda.invoke(params, function(err, data) {
        self.progress = false;
        if (err) {
        } else {
          var json = JSON.parse(data.Payload.toString());
          if (json.message == "success") {
            self.table.setData(json.songDatas);
          } else {
          }
        }
      });
    }
  }
};
</script>

