<template>
  <v-dialog v-model="dialog" :max-width="options.width" @keydown.esc="cancel">
      <v-card>
        <v-card-title>
            <span class="headline">Login</span>
        </v-card-title>
        <v-card-text>
            <v-container grid-list-md>
            <v-layout wrap>
                <v-flex xs12>
                    <v-alert
                      :value="errorMessage"
                      type="error"
                    >
                      Unable to login. <br>
                      Check your email or password.
                    </v-alert>
                </v-flex>
                <v-flex xs12>
                  <v-progress-linear :indeterminate="true" v-show="progress"></v-progress-linear>
                </v-flex>
                <v-flex xs12>
                    <v-text-field
                        v-model="email"
                        :rules="[rules.required, rules.email]"
                        label="E-mail"
                    ></v-text-field>
                </v-flex>
                <v-flex xs12>
                    <v-text-field
                        v-model="password"
                        :append-icon="show1 ? 'visibility_off' : 'visibility'"
                        :rules="[rules.required, rules.min]"
                        :type="show1 ? 'text' : 'password'"
                        name="input-10-1"
                        label="Password"
                        hint="At least 8 characters"
                        counter
                        @click:append="show1 = !show1"
                    ></v-text-field>
                </v-flex>
            </v-layout>
            </v-container>
        </v-card-text>
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn flat @click.native="cancel()">Close</v-btn>
            <v-btn color="primary" flat @click.native="login()" :disabled="!validate()">Login</v-btn>
        </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
import AWS from "aws-sdk";

export default {
  data: () => ({
    dialog: false,
    resolve: null,
    reject: null,
    message: null,
    title: null,
    e1: 1,
    options: {
      color: "primary",
      width: 400
    },
    email: "",
    password: "",
    errorMessage: null,
    show1: false,
    progress: false,
    rules: {
      required: value => !!value || "Required.",
      counter: value => value.length <= 20 || "Max 20 characters",
      min: v => v.length >= 8 || "Min 8 characters",
      email: value => {
        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return pattern.test(value) || "Invalid e-mail.";
      }
    }
  }),
  methods: {
    validate: function() {
      return (
        this.rules.required(this.email) == true &&
        this.rules.required(this.password) == true &&
        this.rules.min(this.password) == true &&
        this.rules.email(this.email) == true
      );
    },
    login: function() {
      this.progress = true;
      this.errorMessage = null;
      let email = this.email;
      let psw = this.password;
      AWS.config.update({
        accessKeyId: "AKIAIRK5MFBNK6PWYPRQ",
        secretAccessKey: "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"
      });
      AWS.config.region = "us-east-1";
      let lambda = new AWS.Lambda();
      let params = {
        FunctionName: "login_and_responce",
        Payload: JSON.stringify({
          username: email,
          password: psw
        })
      };

      const self = this;
      lambda.invoke(params, function(err, data) {
        self.progress = false;
        if (err) {
          self.errorMessage = JSON.stringify(err, undefined, 1);
          return;
        } else {
          let json = JSON.parse(data.Payload.toString());
          if (json.errorMessage) {
            self.errorMessage = JSON.stringify(json.errorMessage, undefined, 1);
            return;
          }

          if (json.AccessToken && json.RefreshToken) {
            self.$emit('updateToken', {
              accessToken: json.AccessToken,
              refreshToken: json.RefreshToken,
              userId: json.UserId,
            });
          }

          self.clearForm();
          self.dialog = false;
        }
      });
    },
    open() {
      this.dialog = true;
      return new Promise((resolve, reject) => {
        this.resolve = resolve;
        this.reject = reject;
      });
    },
    agree() {
      this.resolve(true);
      this.clearForm();
      this.dialog = false;
    },
    cancel() {
      this.resolve(false);
      this.clearForm();
      this.dialog = false;
    },
    clearForm: function() {
      this.email = "";
      this.password = "";
      this.errorMessage = null;
    }
  }
};
</script>